.pageWrapper {
  width: 100%;
  height: 100%;
  max-width: 1600px;
  padding: 90px 30px 30px;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

@media only screen and (max-width: 600px) {
  .pageWrapper {
    padding: 90px 5% 5%;
  }
}
